.pizza_input {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);

    @media(orientation: portrait) {
        visibility: hidden; }

    &--unos {
        top: 10%; }

    &--dos {
        top: 50%; }

    &__title {
        color: rgb(61, 186, 204); }

    &__form {
        margin: 10px auto; }

    &__label {
        display: block; }

    &__input {
        width: 100%;
        margin: 0 auto 16px auto;
        padding: 4px;
        transition: .2s;
        font-size: 14px;
        color: #292929;
        border: 2px solid rgb(227, 224, 224);
        border-radius: 10px;
        outline: none;

        &:focus {
            border: 2px solid rgb(61, 186, 204); } } }
